import React, { useEffect } from 'react'
import { Layout } from '../components/layout'
import { techs } from '../db/techs'

const Techs = () => {
  useEffect(() => {
    if (typeof window !== 'undefined') {
      import('../animations/techsAnimation').then(({ techsAnimation }) => techsAnimation())
    }
  }, [])

  return (
    <Layout>
      <div className="techs-wrapper">
        <div className="techs-container">
          <div className="techs-menu">
            {techs.map((t) => {
              return (
                <a className="tech-item" key={t.name} href={t.address} target="_blank">
                  <span className="tech-item-name">
                    <span className="tech-item-name-inner">
                      <i>{t.name}</i>
                    </span>
                  </span>
                  <span className="tech-item-info">{t.info}</span>
                </a>
              )
            })}
          </div>
        </div>
      </div>
    </Layout>
  )
}

export default Techs
